box-sizing: border-box;\n}\n\nimg {\n  -webkit-user-drag: none;\n  user-select: none;\n  -moz-user-select: none;\n  -webkit-user-select: none;\n  -ms-user-select: none;\n}\n\nbody {\n  @include flex-container(column, start, stretch);\n\n  min-height: 100vh;\n  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),\n    url('./assets/images/background.jpg');\n  background-size: cover;\n\n  font-family: 'Josefin Sans', sans-serif;\n}\n\n.header {\n  @include flex-container();\n  background-image: linear-gradient(\n    rgba($clr-primary, 0.3),\n    rgba($clr-primary, 0)\n  );\n  padding: 24px;\n\n  .logo {\n    @include flex-container($gap: 8px);\n    opacity: 90%;\n\n    &__icon {\n      height: 48px;\n      filter: drop-shadow(0 0 2px #ffffff77);\n    }\n\n    &__text {\n      padding-top: 5.5%;\n      color: #fff;\n    }\n  }\n}\n\n.main {\n  display: grid;\n  flex: 1;\n  grid-template: 50px 1fr / 1fr 300px 300px;\n  grid-template-areas:\n    'top-bar daily hourly'\n    'current daily hourly';\n\n  & > * {\n    border: 1px solid red;\n  }\n\n  .top-bar {\n    grid-area: top-bar;\n\n    @include flex-container(row, start, center, 16px);\n    padding: 12px;\n\n    .search-box-wrapper {\n      @include flex-container(row, start, center, 4px);\n\n      #search-box {\n        padding: 6px 8px;\n\n        background-color: transparent;\n        opacity: 80%;\n        outline: none;\n        border: none;\n        border-bottom: 2px solid white;\n\n        color: white;\n\n        &::placeholder {\n          color: #ffffffcc;\n        }\n      }\n\n      .search-btn {\n        appearance: none;\n        outline: none;\n        border: none;\n        @include button(24px, 24px, 90%);\n\n\n        img {\n          filter: invert(0.9);\n        }\n      }\n    }\n\n  \n    .temp-switch {\n      $height: 24px;\n      $width: 48px;\n      $border-width: 2px;\n  \n      position: relative;\n  \n      height: $height;\n      width: $width;\n  \n      @include box-shadow(#00000055, 0, 0, 2px, 0);\n      border: $border-width solid #ffffffcc;\n      border-radius: 32px;\n      \n      cursor: pointer;\n      overflow: hidden;\n  \n      &__checkbox {\n        display: none;\n  \n        &:checked ~ .temp-switch__slider {\n          background-color: transparent;\n        }\n        \n        &:checked ~ .temp-switch__slider:before {\n          transform: translateX(calc($width - $height));\n        }\n\n        &:checked ~ p{\n          transform: translateX(calc(-1 * ($width - $height - 6px)));\n\n          &::before {\n            content: \"°F\";\n          }\n        }\n      }\n  \n      &__slider {\n        position: absolute;\n        top: 0;\n        left: 0;\n        bottom: 0;\n        right: 0;\n        \n        background-color: transparent;\n        transition: 0.3s;\n  \n        &::before {\n          content: \"\";\n          position: absolute;\n          height: calc($height - 8px);\n          width: calc($height - 8px);\n          left: 4px - $border-width;\n          bottom: 4px - $border-width;\n  \n          border-radius: 50%;\n          background-color: white;\n          transition: .4s;\n        }\n      }\n\n      &__label {\n        position: absolute;\n        right: 6px;\n        bottom: 1px;\n        transition: 0.3s;\n        \n        &::before {\n          content: \"°C\";\n          color: #ffffffdd;\n          font-size: 0.9rem;\n          font-weight: bolder;\n        }\n      }\n    }\n  }\n\n  .current {\n    grid-area: current;\n  }\n\n  .daily {\n    grid-area: daily;\n    margin: 24px 36px;\n  }\n\n  .hourly {\n    grid-area: hourly;\n    margin: 24px 36px;\n  }\n}\n"],"sourceRoot":""}]);
